<template>
    <div>
        <Row>
            <Button type="primary" @click="add">
                <Icon type="plus"></Icon>新增渠道
            </Button>
        </Row>
        <Row class="margin-top-10">
            <Table :columns="table.cols" :data="table.data"></Table>
        </Row>

        <Modal
            v-model="model"
            :mask-closable="false"
            :title="modelTitle"
            :loading="loadingBtn"
            @on-ok="addOrUpdate"
            @on-cancel="cancel"
        >
            <Form ref="form" :model="vo" :label-width="80" :rules="ruleValidate">
                <FormItem label="组名" prop="name">
                    <Input v-model="vo.name"/>
                </FormItem>
                <FormItem label="描述" prop="description">
                    <Input
                        v-model="vo.description"
                        type="textarea"
                        :autosize="{minRows: 2,maxRows: 5}"
                        placeholder="请输入使用须知"
                    />
                </FormItem>
                <FormItem label="利润类型" prop="profitType">
                    <Select v-model="vo.profitType" placeholder="选择利润类型">
                        <Option :value="2">数值</Option>
                        <Option :value="1">百分比</Option>
                    </Select>
                </FormItem>
                <FormItem label="利润值" v-if="vo.profitType ===1" prop="profit">
                    <InputNumber
                        style="width: 408px"
                        :max="100"
                        :min="0"
                        v-model="vo.profit"
                        :formatter="value => `${value}%`"
                        :parser="value => value.replace('%', '')"
                    />
                </FormItem>
                <FormItem label="利润值" v-if="vo.profitType ===2" prop="profit">
                    <InputNumber
                        style="width: 408px"
                        :min="0"
                        v-model="vo.profit"
                        :formatter="value =>  `${value/100}`"
                        :parser="value => `${parseInt((value*100).toFixed(2))}`"
                    ></InputNumber>
                </FormItem>
            </Form>
        </Modal>
    </div>
</template>
<script>
import TableDataView from '@/mixins/table-data-view';
import TableColRender from '@/mixins/table-col-render';
import ModelView from '@/mixins/model-view';

export default {
    name: 'DistGroup',
    mixins: [TableColRender, TableDataView, ModelView],
    data () {
        return {
            ruleValidate: {
                name: [{ required: true, message: '不能为空', trigger: 'blur' }]
            },
            profit: null,
            table: {
                url: '/api/channel/group/list',
                cols: [
                    {
                        title: '组名',
                        key: 'name'
                    },
                    {
                        title: '利润类型',
                        key: 'profitType',
                        render: (h, params) => (
                            <span> {params.row.profitType === 1 ? '百分比' : '数值'} </span>
                        )
                    },
                    {
                        title: '利润值',
                        key: 'profit'
                    },
                    {
                        title: '创建时间',
                        key: 'createTime',
                        render: this.renderDate
                    },
                    {
                        title: '操作',
                        width: 300,
                        render: (h, params) =>
                            this.renderActions(h, params, [
                                {
                                    config: {
                                        loading: this.loadingBtn,
                                        style: { marginRight: '5px' }
                                    },
                                    click: () => this.update(params.row),
                                    text: '编辑'
                                },
                                {
                                    config: {
                                        loading: this.loadingBtn,
                                        style: { marginRight: '5px' }
                                    },
                                    click: () => this.selectEpMember(params.row),
                                    text: '成员'
                                },
                                {
                                    config: {
                                        loading: this.loadingBtn,
                                        style: { marginRight: '5px' }
                                    },
                                    click: () => this.saleUp(params.row),
                                    text: '商品上架'
                                },
                                {
                                    config: {
                                        loading: this.loadingBtn,
                                        style: { marginRight: '5px' }
                                    },
                                    click: () => this.saleDown(params.row),
                                    text: '商品下架'
                                }
                            ])
                    }
                ],
                data: []
            },
            vo: {
                name: null,
                description: null,
                profitType: 1,
                profit: null
            },
            addUrl: '/api/channel/group/add',
            updateUrl: '/api/channel/group/update',
            removeUrl: '/api/channel/group/delete'
        };
    },
    watch: {
        profit: {
            handler (val) {
                this.vo.profit = parseInt((val * 100).toFixed(0));
            }
        }
    },

    methods: {
        selectEpMember (row) {
            this.$router.push({ name: 'DistUpEp', params: { channelId: row.id } });
        },
        saleUp (row) {
            this.$router.push({
                name: 'DistUpSubPro',
                params: { channelId: row.id }
            }); // profitType:row.profitType,profit:row.profit
        },
        saleDown (row) {
            this.$router.push({
                name: 'DistDownSubPro',
                params: { channelId: row.id }
            });
        }
    }
};
</script>
